const CORPSITE_API_PREFIX = "https://qualys-marketing-api.herokuapp.com";

let contentful;
let proxy = false;
if (typeof require !== "undefined") {

	require("cross-fetch/polyfill");
	contentful = require("contentful");

	// proxy code
	if (process.env["HTTPS_PROXY"]) {
		let url = new URL(process.env["HTTPS_PROXY"]);
		proxy = {
			host: url.hostname,
			port: url.port
		};
	}
	// NOTE global-tunnel-ng seems to remove the proxy environment variables
	require("global-tunnel-ng").initialize();

} else {
	contentful = window.contentful;
}


