/*
 * helper class to handle HTTP response errors
 */
class HTTPResponseError extends Error {
	constructor(response) {
		super();
		this.url = response.url;
		this.status = response.status;
		this.statusText = response.statusText;
		this.message = `request to ${this.url} failed, reason: HTTP ${this.category} ${this.status} ${this.statusText}`;
		this.name = "HTTPResponseError";
	}
	get category () {
		const key = Math.floor(this.status / 100) + "xx";
		return this.constructor.categories[key];
	}
}
HTTPResponseError.categories = {
	"1xx": "Informational",
	"2xx": "Successful",
	"3xx": "Redirection",
	"4xx": "Client Error",
	"5xx": "Server Error"
};


/*
 * collect data for the Homepage
 */
function fetchHomepageData () {

	return Promise.all([
		fetchRecentContent(),
		fetchTrendingContent(),
		fetchTrendingPeople(),
		fetchHomepageVideos(),
		fetchWhatsNew(),
		fetchTopArticles(),
		fetchBlogPosts(),
		fetchNotifications(),
		fetchRecentQIDs()
	]).then(results => {
		return Promise.resolve({
			content: results[0],
			trending: {
				content: results[1],
				people: results[2]
			},
			videos: results[3],
			news: results[4],
			articles: results[5],
			posts: results[6],
			notifications: results[7],
			qids: results[8]
		});
	});

}


/*
 * collect data for the app channel pages
 */
function fetchChannelData (channel) {

	return Promise.all([
		fetchRecentContent({ topicId: channel.salesforceTopicId }),
		fetchTrendingContent({ topicId: channel.salesforceTopicId }),
		fetchTrendingPeople({ topicId: channel.salesforceTopicId }),
		fetchChannelVideos({ channel_id: channel.vimeoChannelId }),
		fetchWhatsNew({ tags: channel.tags }),
		fetchTopArticles({ tags: channel.tags }),
		fetchRecentQIDs({ urlSlug: channel.urlSlug })
	]).then(results => {
		return Promise.resolve({
			content: results[0],
			trending: {
				content: results[1],
				people: results[2]
			},
			videos: results[3],
			news: results[4],
			articles: results[5],
			qids: results[6]
		});
	});

}


/*
 * fetch recent QIDs from JSON data file
 */
function fetchRecentQIDs(options) {

	var number = 4;

	if (typeof require !== "undefined") {

		return require("./vulnerability-detection-pipeline/qids.json.data.js").then(data => {

			return data.qids.slice(0, number);

		});

	} else {

		let endpoint = "vulnerability-detection-pipeline/qids.json";
		if (options && options.urlSlug) {

			endpoint = `../${endpoint}`;

		}

		return fetch(endpoint).then(res => {

			if (!res.ok) {
				throw new HTTPResponseError(res);
			}

			return res.json();

		}).then(data => {

			return data.slice(0, number);

		});

	}

}


//const wordpressPrefix = "https://public-api.wordpress.com/rest/v1.1/sites/blog.qualys.com";
let wordpressPrefix = `${CORPSITE_API_PREFIX}/wordpress`;


/*
 * fetch blog posts from WordPress
 */
function fetchBlogPosts (options) {

	let number = 3;
	let endpoint = `${wordpressPrefix}/posts/`;

	// tell wordpress exactly which fileds we want
	endpoint += "?fields=ID,date,URL,title,author,content,categories";

	if (options && options.number) {

		number = encodeURIComponent(options.number);

	}
	endpoint += "&per_page=number";

	return fetch(endpoint).then(res => {

		if (!res.ok) {
			throw new HTTPResponseError(res);
		}
		return res.json().then(data => {

			let posts = data.posts;

			// trim to number requested
			return posts.slice(0, number);

		});

	});

}

//const wordpressNotificationsPrefix = "https://public-api.wordpress.com/rest/v1.1/sites/notifications.qualys.com";
let wordpressNotificationsPrefix = `${CORPSITE_API_PREFIX}/notifications`;

/*
 * fetch notifications from WordPress
 */
function fetchNotifications(options) {

	let number = 4;
	let endpoint = `${wordpressNotificationsPrefix}/posts/`;

	if (options && options.number) {

		number = encodeURIComponent(options.number);

	}

	endpoint += `?number=${number}`;

	// tell wordpress exactly which fileds we want
	endpoint += "&fields=ID,date,URL,title";

	return fetch(endpoint).then(res => {

		if (!res.ok) {
			throw new HTTPResponseError(res);
		}
		return res.json().then(data => {

			let notifications = data.posts;

			return notifications;

		});

	});

}


/*
 * helper
 */
function createContentfulClient () {

	let url = new URL(CORPSITE_API_PREFIX);
	let params = {
		space: "5pe4or9i232n",
		accessToken: "the contentful library requires this field; token is actually provided by our api proxy",
		insecure: url.protocol !== "https:",
		host: url.host,
		basePath: "/contentful/",
		proxy: proxy
	};

	// Contentful SDK 7.3.0 seems to ignore localhost
	if (url.hostname === "localhost") {
		params.host = `127.0.0.1:${url.port}`;
	}

	return contentful.createClient(params);

}
function handleContentfulClientError (err) {

	if (err.response) {

		// add the expected url property on the response
		err.response.url = err.response.request.res.responseUrl;
		err = new HTTPResponseError(err.response);

	}

	throw err;

}


/*
 * fetch What's New links from Contentful
 * (optionally) filter by tag(s)
 */
function fetchWhatsNew (options) {

	const client = createContentfulClient();
	const defaultQuery = {
		"content_type": "news",
		"limit": 3,
		"order": "-fields.publicationDate,-sys.updatedAt,-sys.createdAt"
	};
	const query = Object.assign({}, defaultQuery, options);

	if (query.tags) {
		query["fields.tags[in]"] = query.tags.join(",");
		delete query.tags;
	}

	return client.getEntries(query).catch(handleContentfulClientError);

}


/*
 * fetch Top Article links from Contentful
 * TODO (optionally) filter by tag(s)
 */
function fetchTopArticles (options) {

	const client = createContentfulClient();
	const defaultQuery = {
		"content_type": "featuredArticle",
		"limit": 3,
		"order": "-fields.publicationDate,-sys.updatedAt,-sys.createdAt"
	};
	const query = Object.assign({}, defaultQuery, options);

	if (query.tags) {
		query["fields.tags[in]"] = query.tags.join(",");
		delete query.tags;
	}

	return client.getEntries(query).catch(handleContentfulClientError);

}


/*
 * fetch videos from Vimeo
 * https://api.vimeo.com/users/{user_id}/videos
 * let our proxy handle authentication
 */
function fetchVideos (options) {

	var per_page = options.per_page || 4;
	var channel_ids = options.channel_ids || [];

	return Promise.all(channel_ids.map(channel_id => {

		return fetchChannelVideos({ channel_id: channel_id});

	})).then(videos => {

		// filter out false-y values
		videos = videos.filter(video => video);

		// get videos out of responses
		videos = videos.map(video => video.data);

		// flatten array
		videos = [].concat(...videos);

		// sort by date
		videos.sort((a, b) => {

			a = a.release_time;
			b = b.release_time;

			if (a < b) {
				return 1;
			} else if (a > b) {
				return -1;
			} else {
				return 0;
			}

		});

		// trim to first four videos
		if (videos.length > per_page) {
			videos = videos.slice(0, per_page);
		}

		return Promise.resolve({ data: videos });

	});

}


/*
 * fetch a bunch of videos from the Community Homepage channel
 * and "round robin" through the list of videos
 */
function fetchHomepageVideos (options) {

	const defaults = {
		"channel_id": 1472579,
		"per_page": 4
	};

	options = Object.assign({}, defaults, options);

	// save original per_page value and request five times as many
	const per_page = options.per_page;
	options.per_page = 5 * per_page;

	// hard limit of 30 videos
	// since we want to rotate through the videos
	// at 30 videos, each video gets 2 minutes
	// and the build typically takes up to 90 seconds
	// so, that should save us from the infinite-build-loop
	if (options.per_page > 30) {
		options.per_page = 30;
	}

	return fetchChannelVideos(options).then(response => {

		var deleteCount, deletedElements, minutes, minutesPerItem;

		// rotate through the videos
		minutesPerItem = 60 / response.data.length;
		minutes = (new Date()).getMinutes();
		deleteCount = Math.floor(minutes / minutesPerItem);

		// shift N items from the start to the end
		deletedElements = response.data.splice(0, deleteCount);
		response.data = response.data.concat(deletedElements);

		// limit to the original per_page value
		response.data = response.data.slice(0, per_page);

		return response;

	});

}

/*
 * fetch videos from Vimeo channels
 * let our proxy handle authentication
 * use Vimeo channels for each app to curate videos
 * order by default channel order
 */
function fetchChannelVideos (options) {

	let direction = "";
	let per_page = 4;
	let sort = "default";
	let channel_id = options.channel_id;
	if (!channel_id) {

		//console.warn(`Invalid Vimeo channel id: ${channel_id}`);
		return Promise.resolve(null);

	}
	let vimeoPrefix = `${CORPSITE_API_PREFIX}/vimeo`;
	let endpoint = `${vimeoPrefix}/channels/${channel_id}/videos`;

	// use field filtering and get double the normal rate-limiting
	let fields = [
		"uri", "name", "description", "link",
		"release_time", "pictures", "metadata"
	].join(",");

	if (options && options.per_page) {

		per_page = encodeURIComponent(options.per_page);

	}
	endpoint += `?per_page=${per_page}`;

	if (options && options.direction) {

		direction = encodeURIComponent(options.direction);

	}
	if (direction) {
		endpoint += `&direction=${direction}`;
	}

	if (options && options.sort) {

		sort = encodeURIComponent(options.sort);

	}
	if (sort) {
		endpoint += `&sort=${sort}`;
	}

	if (options && options.fields) {

		fields = encodeURIComponent(options.fields);

	}
	endpoint += `&fields=${fields}`;

	if (options && options.page) {

		let page = encodeURIComponent(options.page);
		endpoint += `&page=${page}`;

	}

	if (options && options.query) {

		let query = encodeURIComponent(options.query);
		endpoint += `&query=${query}`;

	}

	return fetch(endpoint).then(res => {

		if (!res.ok) {
			throw new HTTPResponseError(res);
		}
		return res.json();

	}).then(data => {

		// find and fetch credits
		const creditDownloads = data.data.map(d => {
			if (d.metadata.connections.credits.total > 1) {

				// fetch additional credits
				// TODO use field filtering and get double the normal rate-limiting
				let fields = [ "role", "name" ].join(",");
				return fetch(`${vimeoPrefix}${d.metadata.connections.credits.uri}?fields=${fields}`).then(res => {

					if (!res.ok) {
						throw new HTTPResponseError(res);
					}

					return res.json().then(credits => {

						// copy full credits into placeholder in video data
						Object.assign(d.metadata.connections.credits, credits);
						return Promise.resolve(d);

					}).catch(err => Promise.reject(err));

				}).catch(err => Promise.reject(err));

			} else {

				return Promise.resolve(d);

			}

		});
		return Promise.all(creditDownloads).then(() => Promise.resolve(data));

	});

}


const sfdcPrefix = `${CORPSITE_API_PREFIX}/sfdc`;

/*
 * fetch recent discussions (optionally filter by tags) from Salesforce
 */
function fetchRecentContent (options) {

	let endpoint = `${sfdcPrefix}/recent-feed-items`;
	let count = 10;
	let tags = [];
	let topicId;

	if (options && options.count) {

		count = encodeURIComponent(options.count);

	}
	endpoint += `?pageSize=${count}`;

	if (options && options.tags) {

		tags = options.tags.map(encodeURIComponent);
		endpoint += `&topics=(${tags.join(",")})`;

	}

	if (options && options.topicId) {

		topicId = encodeURIComponent(options.topicId);
		endpoint += `&topicId=${topicId}`;

	}

	return fetch(endpoint).then(res => {

		if (!res.ok) {
			throw new HTTPResponseError(res);
		}
		return res.json();

	});

}


